/** @jsx jsx */
import React from 'react'
import {Global, css, jsx } from '@emotion/core';
import {Link} from 'react-router-dom';

import tictac from "../img/tictac.png"


 const Portfolio =() => {
    return (<div>
      
        <div css={css`display: grid;
        grid-template: repeat(3, 1fr) / repeat(3, 1fr);`}>
          
            <div css={css`width:auto; height: auto; margin-top:30px;`}>
        <Link to="/game"><img src={tictac} css={css`width: 30vw; height: 30vh`} />
     
        </Link>  
        <h3 css={css`text-align: center;`}>tictactoe</h3>
        </div>
            
       
        
        </div>
        <h1>More items will be added soon...</h1>
        </div>
    )
}


export default Portfolio

